"use client";

import React, { useState } from "react";
import { signIn } from "./auth"; // Assuming signIn function is shared
import App from "./page"; // Import the application
import { setIdToken } from './client';
import Link from 'next/link';

export default function DashboardLayout({ children }) {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');
  const [username, setUsername] = useState('');

  const menuItems = [
    { name: 'Home', path: '/' },
    { name: 'Boards', path: '/boards' },
    { name: 'Tasks', path: '/tasks' },
  ];

  async function handleLogin(event) {
    event.preventDefault();
    const form = event.currentTarget;

    try {
      const username = form.elements.email.value;
      const password = form.elements.password.value;

      // Sign in with AWS Cognito
      const user = await signIn(username, password);
      console.log("User signed in:", user);
      console.log("ID token:", user.idToken.jwtToken);
      setIdToken(user.idToken.jwtToken); // Set the ID token for the client
      setIsLoggedIn(true);
      setUsername(username); // Set the username after successful login
      setErrorMessage(''); // Clear errors on success
    } catch (error) {
      console.error("Error signing in:", error);
      setErrorMessage("Sign in failed. Please check your credentials."); // Show error message
    }
  }

  return (
    <html lang="en">
      <body>
        <header style={{ display: 'flex', justifyContent: 'space-between', padding: '10px', borderBottom: '1px solid #ccc' }}>
          {isLoggedIn ? (
            <div>Welcome, {username}!</div>
          ) : (
            <form onSubmit={handleLogin} style={{ display: 'flex', alignItems: 'center' }}>
              <label htmlFor="email">Email:</label>
              <input type="text" id="email" name="email" />
              <label htmlFor="password">Password:</label>
              <input type="password" id="password" name="password" />
              <input type="submit" value="Login" />
              {errorMessage && <p style={{ color: 'red', marginLeft: '10px' }}>{errorMessage}</p>}
            </form>
          )}
        </header>
        <main>
          {isLoggedIn ? 
            <div className="flex">
              <div className="w-1/4 bg-gray-800 text-white p-4">
                <h2 className="text-xl font-bold mb-4">Menu</h2>
                <ul>
                  {menuItems.map((item) => (
                    <li key={item.path} className="mb-2">
                      <Link href={item.path}>
                        <a className="block p-2 rounded hover:bg-gray-700">{item.name}</a>
                      </Link>
                    </li>
                  ))}
                </ul>
              </div>
              <div className="w-3/4 p-6">
                {children}
              </div>
            </div>
            : 
            <p>Please log in to access application features.</p>}
        </main>
      </body>
    </html>
  );
}